<template>
	<view>
		<!-- 状态栏 -->
		<view class="carousel-section">
			<hx-navbar class="navHeader" :title="hint" :fixed="true" :back="false" :background-color="bgcolor" color="#000" />
			<view v-if="showHeader" class="status" :style="{ position: headerPosition,top:statusTop,opacity: afterHeaderOpacity}"></view>
			<!-- 顶部导航栏 -->
			<view v-if="showHeader" class="header" :style="{ position: headerPosition,opacity: afterHeaderOpacity,top:headerTop,background:backgroundColorRgb}">
				<!-- logo -->
				<view class="addr">
					<!-- @tap="toStore" -->
					<text>{{storeName}}</text>
					<tui-icon name="position-fill" color="#fff" size="18"></tui-icon>
				</view>
				<!-- 搜索框 -->
				<view class="input-box">
					<input placeholder="熙美诚品" placeholder-style="color:#c0c0c0;" @tap="toSearch()" />
					<view class="icon search"></view>
				</view>
			</view>
			<!-- 标题栏和状态栏占位符 -->
			<view class="titleNview-placing" :style="{'background': backgroundColorRgb}"></view>
			<!-- 背景色区域 -->
			<!-- :style="{backgroundColor:titleNViewBackground}" -->
			<view class="titleNview-background" :style="{'background': backgroundColorRgb}"></view>
			<swiper class="carousel" circular @change="swiperChange" :style="{ height: getheight} " autoplay="true">
				<swiper-item v-for="swiper in swiperList" :key="swiper.id" class="carousel-item">
					<image :src="swiper.img" @tap="toSwiper(swiper)" lazy-load="true"></image>
				</swiper-item>
			</swiper>
			<!-- 自定义swiper指示器 -->
			<view class="swiper-dots">
				<text class="num">{{swiperCurrent+1}}</text>
				<text class="sign">/</text>
				<text class="num">{{swiperList.length}}</text>
			</view>
		</view>
		<view class='trait'>
			<view class='trait-item' v-for="(item,index) in traitList" :key="index">
				<image :src='traitListImg[index]' class='pp-img' lazy-load="true"></image>{{item}}
			</view>
		</view>
		<!-- 分类列表 -->
		<!-- <view class="category-list">
			<view class="category" v-for="(row, index) in categoryList" :key="index" @tap="toCategory(row.id)">
				<view class="img">
					<image :src="row.icon"></image>
				</view>
				<view class="text">{{ row.cate_name }}</view>
			</view>
		</view> -->
		<scroll-view class="catescroll-view" scroll-x="true" @bindscroll="scroll" :scroll-left="scrollLeft">
			<view class="catescroll-view-item" v-for="(row, index) in categoryList" :key="index" @tap="toCategory(row)">
				<view class="img">
					<image :src="row.icon"></image>
				</view>
				<view class="text">{{ row.cate_name }}</view>
			</view>
		</scroll-view>
		<!-- <view class="" v-if="bartainlist.length!=0" @tap="toBargain">
			<view class="line"></view>
			<view class="info-three" style="display: flex;justify-content: space-between;align-items: center;">
				<view class="title">
					<text class="main" style="font-size: 36rpx;">劲爆砍价</text>
				</view>
				<view class="" style="font-size: 26rpx;color: #b2b2b2;">
					查看全部<tui-icon name="arrowright" color="#b2b2b2" size="20"></tui-icon>
				</view>
			</view>
			<scroll-view class="scroll-view" scroll-x="true" @bindscroll="scroll" :scroll-left="scrollLeft">
				<view class="scroll-view-item" v-for="(item,index) in bartainlist" :key="index">
					<image :src="item.pro_img" mode="" class="pImg"></image>
					<text class="pTitle title-one">{{item.pro_name}}</text>
					<view class="detail">
						<view class="price">
							<view class="rel"><text style="font-size: 26rpx;">￥</text>{{item.dis_price}}</view>
							<view class="del">￥{{item.pro_price}}</view>
						</view>

					</view>
				</view>
			</scroll-view>
		</view> -->
		<!-- 主题图片 -->
		<view class="info-three">
			<view class="" v-for="(item,index) in actionBanner" :key="index" :style="{ height: item.h+'rpx'} ">
				<image :src="item.src" mode="" class="big" @tap="toPage(item)"></image>
			</view>
		</view>
		<!-- 活动专题一 -->
		<view class="spec-box" v-if="seckillList.length>0 || assembleList.length>0">
			<view class="spec-info">
				<view class="left" @tap="toSeckill()">
					<view class="title">
						<text class="main">限时秒杀</text>
						<tui-countdown :time="seckillTime" color="#fff" bcolor="#e41f19" bgcolor="#e41f19" colonColor="#e41f19" style="margin-left: 10rpx;margin-top: 6rpx;"></tui-countdown>
					</view>
					<view class="subtxt">
						拼手速 抢低价好物
					</view>
					<view class="product" v-if="seckillList.length>0">
						<view class="one" v-for="(item,index) in seckillList" :key="index">
							<image :src="item.pro_sn" mode="" class="pImg"></image>
							<view class="price">
								<view class="dis"><text style="font-size: 20rpx;">￥</text>{{item.dis_price | toFixed }}</view>
								<view class="pro">￥{{item.pro_price | toFixed}}</view>
							</view>
						</view>
					</view>
					<view class="product" v-else>
						<image src="../../static/img/seckill.jpg" mode="" style="width: 380rpx; height: 240rpx;"></image>
					</view>
				</view>
				<view class="right" @tap="toGroup()">
					<view class="title">
						<text class="main">超值拼团</text>
						<view class="txt">低至3折</view>
					</view>
					<view class="subtxt">
						发现更多优惠好物
					</view>
					<view class="product" v-if="assembleList.length>0">
						<view class="one" v-for="(item,index) in assembleList" :key="index">
							<image :src="item.pro_sn" mode="" class="pImg"></image>
							<view class="price">
								<view class="dis"><text style="font-size: 20rpx;">￥</text>{{item.dis_price | toFixed}}</view>
								<view class="pro">￥{{item.pro_price | toFixed}}</view>
							</view>
						</view>
					</view>
					<view class="product" v-else>
						<image src="../../static/img/seckill.jpg" mode="" style="width: 380rpx; height: 240rpx;"></image>
					</view>
				</view>
			</view>
		</view>
		<!-- 活动专区一 -->
		<view class="info-four" style="padding: 10rpx 0;" v-if="specialList.length>0">
			<view class="" v-for="(item,index) in specialList" :key="index">
				<image :src="item" mode="" @tap="toSpeciall(index)" style="width: 335rpx;height: 200rpx;"></image>
			</view>
		</view>
		<!-- 活动专区二 -->
		<view class="info-four">
			<view class="left">
				<image src="http://web.xmcpcn.com/img/smallBanner/01.png" mode="" @tap="toProduct('1')"></image>
			</view>
			<view class="right">
				<image src="http://web.xmcpcn.com/img/smallBanner/02.png" mode="" @tap="toProduct('2')"></image>
				<image src="http://web.xmcpcn.com/img/smallBanner/03.png" mode="" @tap="toProduct('3')"></image>
			</view>
		</view>
		<view class="line"></view>
		<view class="info-three" style="display: flex;justify-content: space-between;align-items: center;">
			<view class="title">
				<text class="main">新品上市</text>
				<text class="line"></text>
				<text class="txt">精选好货</text>
			</view>
			<view class="more" style="font-size: 26rpx; color: #B2B2B2;" @tap="tonewGood">查看更多</view>
		</view>
		<!-- 商品列表 -->
		<view class="goods-list">
			<view class="product-list" style="padding-bottom: 0;">
				<view class="product" v-for="(item,index) in productList" :key="index">
					<view class="imgBox">
						<image mode="widthFix" :src="item.pro_sn" @tap="toGoods(item)"></image>
						<view class="sign" v-if="item.isBOGO==1">
							买一送一
						</view>
					</view>
					
					<view class="name title-one" @tap="toGoods(item)">{{ item.pro_name }}</view>
					<view class="info">
						<view class="price-info">
							<view class="price" v-if="item.dis_price!==undefined" @tap="toGoods(item)">
								<text style="font-size: 24rpx;">¥</text>{{ item.dis_price }}
								<text class="dis_price" v-if="item.dis_price!==undefined">{{ item.pro_price }}</text>
							</view>
							<view class="price" v-else @tap="toGoods(item)">
								<text style="font-size: 24rpx;">¥</text>{{ item.pro_price }}
							</view>
							<view class="buy" @tap="tobuy(item)">
								<image src="../../static/img/cart.png" mode="" class="cart"></image>
								<!-- <text class="cartnum" v-if="item.itemcount>0">{{item.itemcount}}</text> -->
							</view>
						</view>
					</view>					
				</view>
			</view>
			<view class="" style="font-size: 26rpx;color: #B2B2B2;padding-bottom:30rpx;text-align: center;" @tap="tonewGood">
				查看更多
			</view>
		</view>
		<!-- 分隔条 -->
		<view class="line"></view>
		<view class="info-three">
			<view class="title">
				<text class="main">超值优惠</text>
			</view>
		</view>
		<view class="goodsList">
			<view class="part" v-for="(item,index) in bargainList" :key="index">
				<view class="imgBox">
					<image mode="widthFix" :src="item.pro_sn" class="goodPic" @tap="toGoods(item)"></image>
					<view class="sign" v-if="item.isBOGO==1">
						买一送一
					</view>
				</view>				
				<view class="content">
					<view class="name title-one" @tap="toGoods(item)">{{ item.pro_name }}</view>
					<view class="hot" @tap="toGoods(item)">
						<view class="txt">
							<image src="../../static/img/hot.png" mode="" style="width: 18rpx; height: 20rpx;"></image>hot
						</view>

					</view>
					<view class="info">
						<view class="price" @tap="toGoods(item)" v-if="item.dis_price!==undefined">
							<text style="font-size: 24rpx;">¥</text>{{ item.dis_price }}
							<text class="dis_price" v-if="item.dis_price!==undefined">{{ item.pro_price }}</text>
						</view>
						<view class="price" @tap="toGoods(item)" v-else>
							<text style="font-size: 24rpx;">¥</text>{{ item.pro_price }}
						</view>
						<view class="buy" @tap="tobuy(item)">
							<image src="../../static/img/cart.png" mode="" class="cart"></image>
							<!-- <text class="cartnum" v-if="item.itemcount>0">{{item.itemcount}}</text> -->
						</view>
					</view>
				</view>
			</view>
		</view>
		<view class="line"></view>
		<view class="info-three">
			<view class="title">
				<text class="main">猜你喜欢</text>
			</view>
		</view>
		<view class="goods-list" style="padding-top: 0;">
			<view class="product-list">
				<view class="product" v-for="(item,index) in Promotion" :key="index">
					<view class="imgBox">
						<image mode="widthFix" :src="item.pro_sn" @tap="toGoods(item)"></image>
						<view class="sign" v-if="item.isBOGO==1">
							买一送一
						</view>
					</view>					
					<view class="name title-one" @tap="toGoods(item)">{{ item.pro_name }}</view>
					<view class="info">
						<view class="price-info">
							<view class="price" v-if="item.dis_price!==undefined" @tap=" toGoods(item)">
								<text style="font-size: 24rpx;">¥</text>{{ item.dis_price }}
								<text class="dis_price" v-if="item.dis_price!==undefined">{{ item.pro_price }}</text>
							</view>
							<view class="price" v-else @tap=" toGoods(item)">
								<text style="font-size: 24rpx;">¥</text>{{ item.pro_price }}
							</view>
							<view class="buy" @tap="tobuy(item)">
								<image src="../../static/img/cart.png" mode="" class="cart"></image>
								<!-- <text class="cartnum" v-if="item.itemcount>0">{{item.itemcount}}</text> -->
							</view>
						</view>

					</view>

				</view>
			</view>
			<view class="loading-text">{{ loadingText }}</view>
		</view>
		<modal :show="modalShow" @cancel="modalShow=false" :custom="true" v-if="popup">
			<view class="popImg">
				<image :src="popup.img" mode="" @tap="modalShow=false" :style="{height:popHeight+'px'}"></image>
			</view>
			<view class="popupBtn">
				<image src="../../static/img/close_btn.png" mode="" @tap="modalShow=false"></image>
			</view>
		</modal>
		<tui-scroll-top :scrollTop="scrollTop"></tui-scroll-top>
	</view>
</template>

<script>
	import QQMapWX from '@/common/utils/qqmap-wx-jssdk.min.js'
	import modal from "@/components/modal/modal"
	import tuiIcon from "@/components/icon/icon"
	import md5 from "@/common/utils/md5.js"
	import tuiCountdown from "@/components/countdown/countdown"
	import {
		ajax,
		getSecret
	} from '@/common/ajax/ajax.js'
	export default {
		onShareAppMessage(res) {
			return {
				title: '熙美诚品',
				path: 'pages/index/index',
				success: (res) => {
					console.log(res);
				},
				fail: (res) => {
					console.log(res);
				}
			}
		},
		components: {
			modal,
			tuiIcon,
			tuiCountdown

		},
		data() {
			return {
				bgcolor: [],
				backgroundColorRgb: 'rgba(255,255,255)',
				headerTop: 0,
				userProvider: '',
				scrollTop: 0,
				getheight: '',
				specialImgHeight: '',
				storeName: '总部旗舰店',
				address: '',
				city: '',
				id: '',
				secret: {},
				showHeader: true,
				afterHeaderOpacity: 1, //不透明度
				headerPosition: 'fixed',
				// headerTop: null,
				statusTop: null,
				nVueTitle: null,
				swiperCurrent: 0,
				// 轮播图片
				swiperList: [],
				// 活动图片
				actionBanner: [],
				// 分类菜单
				categoryList: [],
				// 限时抢购
				seckillList: [],
				// 砍价
				bartainlist: [],
				// 拼团
				assembleList: [],				
				seckillTime: 0,
				// 超值优惠
				bargainList: [],
				//猜你喜欢列表
				Promotion: [],
				// 新品上市
				productList: [],
				// 活动
				specialList: [],
				loadingText: '正在加载中...',
				amapPlugin: null,
				// 定位弹窗
				modalShow: false,
				storeItems: [{
					store_name: '总部旗舰店',
					desc: '由总部统一发货',
					store_code: '10001'
				}],
				// 门店编码
				store: '10001',
				hint: '熙美诚品',
				popup: null,
				// 向左滑动距离
				scrollLeft: 0,
				// 弹窗高度
				popHeight: 0,
				addShow: true,
				// 品牌
				traitList: [],
				traitListImg: [
					'/static/img/pinpai.png',
					'/static/img/baoyou.png',
					'/static/img/jisu.png'
				]
			};
		},
		onPageScroll(e) {
			this.scrollTop = e.scrollTop;
			//兼容iOS端下拉时顶部漂移
			this.headerPosition = e.scrollTop >= 0 ? "fixed" : "absolute";
			// this.headerTop = e.scrollTop >= 0 ? null : 0;
			this.statusTop = e.scrollTop >= 0 ? null : -this.statusHeight + 'px';
		},
		//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
		onPullDownRefresh() {
			this.Promotion=[];
			this.getgoods();
			this.getassembleList();
			this.getPromotionList();
			setTimeout(function() {
				uni.stopPullDownRefresh();
			}, 1000);
		},
		onReachBottom() {
			this.getPromotionList();
			setTimeout(() => {
				uni.stopPullDownRefresh()
			}, 1000)		
		},
		onLoad() {
			this.getbig();
			this.getgoods();			
			this.getassembleList();
			this.getPromotionList();
			this.userProvider = this.$xm.userProvider();
			if (this.userProvider != 'weixin') {
				uni.login({
					success: (res) => {
						console.log(res)
						if (res.code) {
							var code = res.code
							uni.getUserInfo({
								success: (res) => {
									console.log(res)
									uni.setStorage({
										key: 'userInfo',
										data: res.userInfo,
										success: (res) => {
											console.log(res);
										}
									});

								},
								fail: res => {
									console.log(res)
									// 获取失败的去引导用户授权 
								}
							})

						} else {}
					}
				})
			}
			this.getLocation();
		},
		onShow() {
			if (uni.getStorageSync('store')) {
				this.storeName = uni.getStorageSync('store').store_name;
				this.store = uni.getStorageSync('store').store_code
				this.getgoods();
			}
			uni.createSelectorQuery().select(".navHeader").boundingClientRect((data) => {
				this.headerTop = data.height + 'px';
			}).exec()
		},
		filters: {
			toFixed(x) {
				return parseFloat(x).toFixed(1);
			}
		},
		methods: {
			// 获取商品总分类
			getbig() {
				let timestamp = (new Date()).getTime();
				let secret = md5.hex_md5("XM#" + md5.hex_md5(String(timestamp)) + "@secret");
				let data = {
					store: this.store,
					secret: timestamp,
					timestamp: secret
				}
				ajax({
					url: '/Procate/getBig',
					method: 'post',
					data: data,
					success: (res) => {
						this.categoryList = res.data;
					}
				})
			},
			getLocation() {
				uni.getLocation({
					success: (res) => {
						console.log(res);
						this.getCity(res.latitude, res.longitude) //获取当前所在城市						
					},
					fail: (res) => {
						console.log(res);

					}
				})
			},
			// 获取定位城市
			getCity(latitude, longitude) {
				let demo = new QQMapWX({
					key: 'LVHBZ-SOY6W-Z5URP-OC34Q-CLWZH-Y6BCX' // 开发密钥（key）
				});
				demo.reverseGeocoder({
					location: {
						latitude: latitude,
						longitude: longitude
					},
					success: (res) => {
						console.log(res);
						this.city = res.result.ad_info.city;
						let latitude = res.result.location.lat;
						let longitude = res.result.location.lng;
						this.address = res.result.address
						uni.setStorageSync('local', res.result.address);
						// this.getgoods()
						let params = {
							city: this.city,
							from: latitude + ',' + longitude
						}
						this.$xm.post('/Index/getStoreList', params, (res) => {
							this.storeItems = res.data;
							uni.setStorageSync('storeList', res.data)

						})
					},
					fail: function(res) {
						console.log(res)

					},
					complete: function(res) {

					}
				});
			},
			// 获取拼团
			getassembleList() {
				this.$xm.post('/groupbuy/getlist', '', (res) => {
					this.assembleList = res.list.splice(0, 2);
					this.assembleList.forEach((ele) => {
						ele.pro_sn = 'http://img.xmvogue.com/thumb/' + ele.pro_sn + '.jpg?x-oss-process=style/300';
					})
				})
			},			
			// 跳转到砍价
			toBargain() {
				uni.navigateTo({
					url: '../seckill/bargain/bargain'
				})
			},
			// 获取猜你喜欢
			getPromotionList() {
				this.$xm.post('/Cart/guess', '', (res) => {
					res.data.map(ele => {
						ele.pro_sn = 'http://img.xmvogue.com/thumb/' + ele.pro_sn + '.jpg?x-oss-process=style/300';
					});
					this.Promotion = this.Promotion.concat(res.data);
				})
			},
			// 获取商品
			getgoods() {
				this.swiperList = []
				let timestamp = (new Date()).getTime();
				let secret = md5.hex_md5("XM#" + md5.hex_md5(String(timestamp)) + "@secret");
				let data = {
					store: this.store,
					secret: timestamp,
					timestamp: secret
				}
				ajax({
					url: '/Index/getInfo',
					method: 'post',
					data: data,
					success: (res) => {
						let result = res.data;
						if (result.bgcolor) {
							this.bgcolor = result.bgcolor;
							let rgbStr = "linear-gradient(" + 90 + "deg,";
							rgbStr += "rgb(" + this.bgcolor[0] + "),rgb(" + this.bgcolor[1] + "))";
							this.backgroundColorRgb = rgbStr;
						}
						this.traitList = result.trait;
						if (result.popup) {
							this.popup = result.popup;
							this.popHeight = this.popup.height;
						}
						if (uni.getStorageSync('store')) {
							this.modalShow = false;
						} else {
							if (result.popup.img != '') {
								this.modalShow = true;
							} else {
								this.modalShow = false;
							}
						}
						this.getheight = result.banner[0].height + 'rpx';
						this.specialImgHeight = result.special.height + 'rpx';
						this.hint = result.hint
						uni.setNavigationBarTitle({
							title: this.hint
						});
						this.swiperList = result.banner;
						this.swiperList.map(ele => {
							ele.img = result._URL_ + ele.img
						})
